<template>
  <div class="index">
    <div id="left-nav">
      <el-menu
          default-active="0-0"
          class="el-menu-vertical-demo"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b"
          :collapse="isCollapse"
          :default-openeds="['0', '1']"
      >
        <el-submenu v-for="(item, index) in menuList" :key="index" :index="index + ''">
          <template slot="title">
            <i :class="item.icon"></i>
            <span slot="title">{{ item.title }}</span>
          </template>
          <el-menu-item v-for="(el, idx) in item.childList" :key="idx" :index="index + '-' + idx"
                        @click="clickMenuItem(el)">
          <span slot="title">
            {{ el.title }}
          </span>
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </div>
    <div class="right-body">
      <el-menu
          default-active="1"
          class="el-menu-demo"
          mode="horizontal"
          background-color="#545c64"
          text-color="#fff"
          active-text-color="#ffd04b">
        <el-menu-item></el-menu-item>
        <el-menu-item v-for="(el, index) in topMenuList" :index="(index + 1) + ''" @click="$router.push(el.path)" :key="index">{{el.title}}</el-menu-item>
        <el-menu-item style="float: right"  >
          <a href="/">Logout</a>
        </el-menu-item>
      </el-menu>
      <div class="main-body">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'index',
  components: {

  },
  data(){
    return{
      isCollapse: false,
      menuList: [
        {
          title: 'Main',
          icon: 'el-icon-menu',
          childList: [
            {
              title: 'Delivery Service',
              path: '/admin/main/delivery/service',
              topMenuList: [
                {
                  title: 'Service',
                  path: '/admin/main/delivery/service'
                },
                {
                  title: 'Ingredient',
                  path: '/admin/main/delivery/ingredient'
                },
                {
                  title: 'Drone',
                  path: '/admin/main/delivery/drone'
                },
                {
                  title: 'Payload',
                  path: '/admin/main/delivery/payload'
                },
                {
                  title: 'Delivery Service',
                  path: '/admin/main/delivery/delivery-service'
                },
                {
                  title: 'Work For',
                  path: '/admin/main/delivery/work-for'
                }
              ]
            },
            {
              title: 'User',
              path: '/admin/main/user/employee',
              topMenuList: [
                {
                  title: 'Employee',
                  path: '/admin/main/user/employee'
                },
                {
                  title: 'Owner',
                  path: '/admin/main/user/owner'
                },{
                  title: 'User',
                  path: '/admin/main/user/user'
                },
              ]
            },
            {
              title: 'Employee',
              path: '/admin/main/employee/pilot',
              topMenuList: [
                {
                  title: 'Pilot',
                  path: '/admin/main/employee/pilot'
                },
                {
                  title: 'Worker',
                  path: '/admin/main/employee/worker'
                },
                {
                  title: 'Hire/Fire Employee',
                  path: '/admin/main/employee/hire'
                }
              ]
            },
            {
              title: 'Restaurant',
              path: '/admin/main/restaurant/restaurant',
              topMenuList: [
                {
                  title: 'Restaurant',
                  path: '/admin/main/restaurant/restaurant'
                },
                {
                  title: 'Location',
                  path: '/admin/main/restaurant/location'
                }
              ]
            }
          ]
        },
        {
          title: 'Admin Display',
          icon: 'el-icon-s-data',
          childList: [
            {
              title: 'Employee',
              path: '/admin/display/employee/list'
            },{
              title: 'Ingredient',
              path: '/admin/display/ingredient/list'
            },{
              title: 'Location',
              path: '/admin/display/location/list'
            },{
              title: 'Owner',
              path: '/admin/display/owner/list'
            },{
              title: 'Pilot',
              path: '/admin/display/pilot/list'
            },{
              title: 'Service',
              path: '/admin/display/service/list'
            }
          ],
        }
      ],
      topMenuList: []
    }
  },
  mounted() {
    console.log(this.$route.meta.title)
    this.topMenuList = this.menuList[0].childList[0].topMenuList
  },
  methods: {
    clickMenuItem(el){
      this.$router.push(el.path)
      this.topMenuList = el.topMenuList
    }
  }
}
</script>


<style scoped lang="scss">
.index {
  width: 100%;
  height: 100vh;
  display: flex;

  #left-nav {
    height: 100%;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
    flex-direction: row-reverse;

    .el-menu-vertical-demo {
      border: none;
    }

    .el-menu-vertical-demo:not(.el-menu--collapse) {
      width: 200px;
      height: 100%;
    }

    .el-submenu__title > span,
    .el-menu-item span {
      margin-left: 10px;
    }

  }

  .right-body {
    width: 100%;
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;

    .main-body {
      box-sizing: border-box;
      padding: 10px;
    }

    .el-menu-demo {
      box-sizing: border-box;
      padding-left: 20px;
    }
  }

}

</style>

